<template>
<div>
  <b-navbar id="nav" toggleable="lg" type="light" variant="faded">
    <b-navbar-brand href="#">KENO</b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">
        <b-nav-item right>
          {{ userMoney }}
          <img :src="coins" width="25">
        </b-nav-item>
        <b-nav-item-dropdown right>
          <template v-slot:button-content>
            <em>{{ userName }}</em>
          </template>
          <b-dropdown-item v-on:click="toWallet">My wallet</b-dropdown-item>
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item v-on:click="logout" >Sign out</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</div>
</template>
<style>
#nav{
      padding: 10px 50px 0px 60px;
    }
</style>
<script>
import coinsSrc from "../assets/coins.png";
import axios from 'axios';

export default {
    data: function () {
        return {
            coins: coinsSrc,
            userName: "unknown",
            userMoney: "unknown"
        }
    },
    methods:{
      getUser: function() {
        this.userName = this.$store.state.authUser;
        this.getMoney(); 
      },
      getMoney: function(){
        axios({
          method: 'get',
          url: this.$store.state.endpoints.baseUrl + "/cash/",
          headers: {
              Authorization: `Bearer ${this.$store.state.jwt}`,
              'Content-Type': 'application/json'
          }
        })
        .then((response) => {
          var answer = response.data;
          this.userMoney = answer;
        })
        .catch((error) => {
          console.log(error);
        }); 
     },
     logout: function(){
       this.$store.commit('removeToken');
       this.$store.commit('unsetAuthUser');
     },
     toWallet: function(){
       this.$router.push({path:"/wallet"})
     }
    },
    mounted(){
      console.log(this.$store.state.isAuthenticated);
      if(this.$store.state.isAuthenticated){
        this.getUser();  
      }
      else{
        this.$router.push({path: '/signin'})
      }       
    }
}
</script>